body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header-bar {
    background-color: rgb(248, 242, 192);
    position: fixed;
    top: 0;
    width: calc(100% - 12px);
    min-height: 144px;
    z-index: 2;
    padding-left: 6px;
    padding-right: 6px
}

@media (max-width: 767px) {
    
    .header-bar {
        height: 96px;
        min-height: 96px;
        display: -webkit-flex;
        display: flex;
        -webkit-flex-direction: column;
                flex-direction: column;
        -webkit-justify-content: space-evenly;
                justify-content: space-evenly;
        -webkit-align-items: center;
                align-items: center;
    }

    .main-header {
        font-size: 18px;
        line-height: 12px
    }

    .filter-paragraph {
        font-size: 16px;
        margin: 0
    }
}
.mapped-plays-and-footer {
    background-color: rgb(254, 255, 250);
    min-height: 100vh;
}

.mapped-plays {
    padding-top: 144px;
    min-height: calc(100vh - 204px)
}

.footer {
    background-color: rgb(196, 226, 196);
    margin:0;
    padding: 3px
}

a:link {
    font-weight: bold;
    color: #337ab7;
    text-decoration: none
}

a:visited {
    font-weight: bold;
    color: #337ab7;
    text-decoration: none
}

a:hover {
    color: #23527c;
    text-decoration: underline
}  

@media (max-width: 767px) {

    .mapped-plays {
        padding-top: 96px
    }

    .footer {
        margin-top: -1px
    }
    
}

@media (min-width: 1080px) {
    .mapped-plays {
        display: -webkit-flex;
        display: flex;
        -webkit-flex-direction: row;
                flex-direction: row;
        -webkit-flex-wrap: wrap;
                flex-wrap: wrap;
        -webkit-justify-content: space-around;
                justify-content: space-around
    }
}
.tag {
    display: inline-block;
    margin: -0.5px 4px -0.5px 4px;
    border: 1px solid black;
    border-radius: 6px;
    padding: 2px 4px;
    background-color: #00ffff;
    cursor: pointer
}

.tag:hover {
    background-color: #80ffff
}

.tag:active {
    background-color: #b0ffff
}
.play-details {
    min-height: calc(100vh - 144px);
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-align-items: center;
            align-items: center;
}

.play-details-text-only {
    width: 100%
}

img.portrait {
    max-height: calc(100vh - 168px)
}

img.landscape {
    height: calc(100vh - 240px)
}

.play-text {
    width: 55vw
}

.upcoming {
    position: absolute;
    -webkit-transform: translate(-12px,-54px);
            transform: translate(-12px,-54px);
}

.upcoming:hover {
    background: url(data:image/png;base64,) no-repeat 0 0;
    width: 0;
    height: 0;
    padding-right: 50px;
    width:0;
	height:0;
	padding:79px 0 0 113px
}

.upcoming:active {
    background: url(data:image/png;base64,) no-repeat 0 0;
}

.play-title {
    border-radius: 6px;
    width: 95%;
    margin-left: 2.5%;
    margin-right: 2.5%;
    background-color: #cfefef;
    cursor: pointer
}

.play-text-only {
    width: 85%;
    margin-left: 7.5%;
    margin-right: 7.5%;
}

.play-title:hover {
    background-color: #e7f7f7
}

.play-title:active {
    background-color: #f3fbfb  
}

.play-text {
    padding-left: 12px;
}

.song-title {
  display: inline-block;
  margin: -0.5px 4px -0.5px 4px;
  border: 1px solid black;
  border-radius: 6px;
  padding: 2px 4px
}

@media (max-width: 767px) {

    .play-details {
        min-height: calc(100vh - 96px);
        width: 95vw;
        margin-left: 2.5vw;
        margin-right: 2.5vw;
        display: -webkit-flex;
        display: flex;
        -webkit-flex-direction: column-reverse;
                flex-direction: column-reverse;
        -webkit-justify-content: center;
                justify-content: center;
        -webkit-align-items: space-around;
                align-items: space-around;
        border-bottom: 1px solid black;
    }

    .play-text {
        width: 90vw;
        margin-left: 5vw;
        margin-right: 5vw
    }

    .upcoming {
        position: relative;
        margin: 12px 0 -24px 0;
        -webkit-transform: translate(0px,0px);
                transform: translate(0px,0px);
    }

    img.portrait, img.landscape {
        margin: 8px 0 36px 0
    }

}

@media (min-width: 1250px) {
    .play-details {
        width: 46vw
    }
}

@media (min-height: 1080px) {
    .play-details {
        min-height: calc(50vh - 72px)
    }

    img.portrait {
        /* These two values are in 210:297 ratio */
        max-height: calc(50vh - 84px);
        width: calc(35.4vh - 59.4px)
    }
    
    img.landscape {
        height: calc(50vh - 120px)
    }
}
.App {
  text-align: center;
  font-family: Alegreya, serif;
}

.clear-filters {
  display: inline-block;
  margin: -0.5px 4px -0.5px 4px;
  border: 1px solid black;
  border-radius: 6px;
  padding: 2px 4px;
  cursor: pointer;
  background-color: #ffff00
}

.clear-filters:hover {
    background-color: #ffff80
}

.clear-filters:active {
    background-color: #ffffb0
}
